.vetskitchen-product-categories .products {
  display: flex;
  flex-wrap: wrap;
  padding: 0;
  list-style: none;
}
.vetskitchen-product-categories .products li.product {
  position: relative;
  flex: 0 0 100%;
  padding: 10px 15px;
  margin: 0;
  width: 100%;
}
.vetskitchen-product-categories .products li.product a {
  display: block;
  text-align: center;
}
.vetskitchen-product-categories .products li.product a:hover img {
  transform: scale3d(1.1, 1.1, 1);
}
.vetskitchen-product-categories .products li.product a:hover .woocommerce-loop-category__title {
  background: rgba(0, 128, 119, 0.9);
}
.vetskitchen-product-categories .products li.product img {
  height: 355px;
  object-fit: contain;
  transition: transform 1s ease-in-out;
}
.vetskitchen-product-categories .products li.product .woocommerce-loop-category__title {
  position: absolute;
  bottom: 20px;
  left: 50%;
  display: inline-block;
  padding: 12px 30px;
  max-width: 409px;
  font-family: "Signika", sans-serif;
  font-size: 16px;
  font-weight: 600;
  line-height: 1.3;
  color: #ffffff;
  background: rgba(248, 135, 41, 0.9);
  border-radius: 20px;
  transform: translateX(-50%);
  transition: 0.2s ease-in-out;
}
.vetskitchen-product-categories .products li.product .woocommerce-loop-category__title .count {
  display: none;
}
@media screen and (min-width: 992px) {
  .vetskitchen-product-categories .products li.product {
    flex: 0 0 50% !important;
    width: 50% !important;
  }
}
@media screen and (max-width: 767px) {
  .vetskitchen-product-categories .products li.product img {
    height: 250px;
  }
}